<template>
  <ul id="home-nav">
    <li
      v-for="item in titles"
      :key="item.id"
      @click="$emit('navigate-to', item.gender ? item.gender : item.type)"
    >
      <span
        :class="{ active: gender == (item.gender ? item.gender : item.type) }"
        >{{ item.name }}</span
      >
    </li>
  </ul>
</template>

<script>
export default {
  name: "HomeNav",
  props: ["titles", "gender"],
};
</script>

<style lang="less" scoped>
#home-nav {
  font-size: 14px;
  display: flex;
  text-align: center;
  height: 40px;
  .active {
    color: #2495f3;
    font-weight: bold;
    border-bottom: 2px solid #2495f3;
  }
  li {
    flex: 1;
    span {
      display: inline-block;
      // line-height: 38px;
      color: black;
      border-bottom: 2px solid transparent;
      padding: 10px 4px 5px 4px;
    }
  }
}
</style>