import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    rankList: null,
    categories: null,
    maleStorage: {},
    femaleStorage: {},
    currentBook: null, //当前书籍
    currentBookSource: null, //当前书籍源
    currentBookChapters: null, //当前书籍章节
    searchHistory: [], //搜索历史
    isRecommend: true,
    brightness: 1, //亮度
    fontSize: 18, //字体大小
    backgroundColor: 'yellow', //背景颜色
    star: false, //是否显示Header的收藏
    collect: JSON.parse(window.localStorage.getItem("collect")) || [], //书架收藏
  },
  mutations: {
    updateRankList(state, payload) {
      state.rankList = payload;
    },
    updateCategories(state, payload) {
      state.categories = payload;
    },
    updateMaleStorage(state, payload) {
      state.maleStorage[payload.id] = payload.obj;
    },
    updateFemaleStorage(state, payload) {
      state.femaleStorage[payload.id] = payload.obj;
    },
    updateCurrentBook(state, payload) {
      state.currentBook = payload;
    },
    updateCurrentBookSource(state, payload) {
      state.currentBookSource = payload;
    },
    updateCurrentBookChapters(state, payload) {
      state.currentBookChapters = payload;
    },
    updateSearchHistory(state, payload) {
      if (state.searchHistory.includes(payload)) {
        return
      }
      state.searchHistory.unshift(payload);
    },
    resetSearchHistory(state, payload) {
      state.searchHistory = payload;
    },
    updateIsRecommend(state, payload) {
      state.isRecommend = payload;
    },
    updateBrightness(state, payload) {
      state.brightness = payload;
    },
    updateFontSize(state, payload) {
      state.fontSize = payload;
    },
    updateBackgroundColor(state, payload) {
      state.backgroundColor = payload;
    },
    updateStar(state, payload) {
      state.star = payload;
    },
    addCollect: function (state, payload) {
      // console.log(state, payload);
      let bool = true
      state.collect.forEach(ele => {
        if (ele.id == payload.id) {
          bool = false
        }
      })
      if(bool){
        state.collect.push(payload);
      }
    },
    removeCollect: function (state, payload) {
      // console.log(state, payload);
      state.collect = state.collect.filter((item) => item.id !== payload);
    },
  },
  actions: {
  },
  modules: {
  }
})
