<template>
  <li class="book-item" @click="toDetail">
    <div class="pic">
      <img :src="`http://statics.zhuishushenqi.com${book.cover}`" />
    </div>
    <div class="info">
      <div class="title">{{ book.title }}</div>
      <div class="desc" v-if="index == 0">{{ book.shortIntro }}</div>
      <div class="popularity">{{ popularity }}人气</div>
      <div class="category">
        <span>{{ book.minorCate }}</span>
      </div>
      <div class="author" v-if="index == 0">{{ book.author }}</div>
    </div>
  </li>
</template>

<script>
export default {
  name: "BookItem",
  props: ["book", "index"],
  computed: {
    popularity() {
      if (this.book.latelyFollower > 10000) {
        return (this.book.latelyFollower / 10000).toFixed(2) + "万";
      } else {
        return this.book.latelyFollower;
      }
    },
  },
  methods: {
    toDetail() {
      this.$router.push({
        name: "BookDetail",
        query: { id: this.book._id },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.book-item {
  flex-basis: 50%;
  display: flex;
  // flex-wrap: wrap;
  padding-right: 5px;
  margin-bottom: 15px;
  box-sizing: border-box;
  &:first-of-type {
    flex-basis: 100%;
    .info {
      flex: 3;
      margin-right: 5px;
      .category {
        right: 0;
      }
      .desc {
        font-size: 12px;
        color: #999;
        margin-top: 5px;

        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2; /*行数*/
        -webkit-box-orient: vertical;
      }
      .author {
        color: #999;
        font-size: 14px;
        position: absolute;
        bottom: 0;
      }
    }
  }
  .pic {
    flex: 1;
    img {
      display: block;
      width: 81px;
      height: 108px;
    }
  }
  .info {
    flex: 1;
    margin-left: 5px;
    position: relative;
    .title {
      font-size: 14px;
      color: #000;
      line-height: 20px;
      // font-weight: bold;
      // margin-top: 5px;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2; /*行数*/
      -webkit-box-orient: vertical;
    }
    .popularity {
      font-size: 13px;
      color: #B93321;
      margin-top: 8px;
    }
    .category {
      position: absolute;
      bottom: 0;
      font-size: 12px;
      color: #777;
      > span {
        display: inline-block;
        border: 1px solid #ccc;
        padding: 0 4px;
      }
    }
  }
}
</style>