<template>
  <div
    class="store"
    :class="{ male: gender == 'male', female: gender == 'female' }"
  >
    <div class="header">
      <van-search :clearable="false" shape="round" />

      <van-swipe
        class="my-swipe"
        vertical
        :autoplay="3000"
        :show-indicators="false"
        :loop="true"
      >
        <van-swipe-item @click="toSearch">帝霸</van-swipe-item>
        <van-swipe-item @click="toSearch">玄幻帝王召唤系统</van-swipe-item>
        <van-swipe-item @click="toSearch">魅医倾城</van-swipe-item>
        <van-swipe-item @click="toSearch">我可以爆修为</van-swipe-item>
      </van-swipe>
    </div>

    <HomeNav
      :titles="titles"
      :gender="gender"
      @navigate-to="navigate"
    ></HomeNav>

    <div class="swipe">
      <van-swipe class="my-swipe" :show-indicators="false" :autoplay="5000">
        <van-swipe-item
          v-for="(item, index) in gender == 'male' ? maleBanner : femaleBanner"
          :key="index"
          @click="toDetail(item.id)"
        >
          <van-image width="100%" :src="item.img" />
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="list">
      <van-tabbar :fixed="false" active-color="#000" inactive-color="#000">
        <van-tabbar-item
          v-for="(item, index) in lists"
          :key="index"
          @click="item.fn(item.title)"
        >
          <span>{{ item.title }}</span>
          <template #icon>
            <img :src="item.img" />
          </template>
        </van-tabbar-item>
      </van-tabbar>
    </div>

    <HomeBooks
      v-if="gender == 'male' && maleHot"
      :books="maleHot.books"
      @to-list="toList('最热')"
      >男生热门</HomeBooks
    >
    <HomeBooks
      v-else-if="gender == 'female' && femaleHot"
      :books="femaleHot.books"
      @to-list="toList('最热')"
      >女生热门</HomeBooks
    >

    <HomeBooks
      v-if="gender == 'male' && maleFinish"
      :books="maleFinish.books"
      @to-list="toList('完结')"
      >男生完本</HomeBooks
    >
    <HomeBooks
      v-else-if="gender == 'female' && femaleFinish"
      :books="femaleFinish.books"
      @to-list="toList('完结')"
      >女生完本</HomeBooks
    >
  </div>
</template>

<script>
import { mapState } from "vuex";
import HomeNav from "@/components/HomeNav.vue";
import HomeBooks from "@/components/HomeBooks.vue";

export default {
  name: "Store",
  components: {
    HomeNav,
    HomeBooks,
  },
  computed: {
    ...mapState(["rankList"]),
  },
  data() {
    return {
      maleHot: null,
      maleFinish: null,
      femaleHot: null,
      femaleFinish: null,
      gender: "male",
      titles: [
        {
          id: 0,
          name: "男生",
          gender: "male",
          path: "/home/?gender=male",
        },
        {
          id: 1,
          name: "女生",
          gender: "female",
          path: "/home/?gender=female",
        },
      ],
      maleBanner: [
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/0d8dc8bbb17a4c9793706ab12d979697.jpg",
          id: "5dc8bb6e1a9f25ad19391258",
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/574d9d74adae46a7a75a3897414cf365.png",
          id: "508742f0abf1ced53c000024",
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/12ddcc817cdf49f787959bb53e21b584.png",
          id: "536da71c88844484410104a3",
        },
      ],
      femaleBanner: [
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201204/997ab2ca67b0464683a70a38c7984676.png",
          id: "55b9c82a491762a137fc777c",
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201204/c48aa5482db346e9837df790c6cf6116.jpg",
          id: "5c75017ffbbc9854793998c2",
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201127/2b8b5e6886ba472c9d6baea14de138b4.png",
          id: "5ee198afc045c331443b18d8",
        },
      ],
      lists: [
        {
          title: "VIP",
          img: require("@/assets/images/free.png"),
          fn: this.toList,
        },
        {
          title: "潜力",
          img: require("@/assets/images/new.png"),
          fn: this.toList,
        },
        {
          title: "热搜",
          img: require("@/assets/images/hot.png"),
          fn: this.toList,
        },
        {
          title: "留存",
          img: require("@/assets/images/finish.png"),
          fn: this.toList,
        },
        {
          title: "排行",
          img: require("@/assets/images/short.png"),
          fn: this.toRank,
        },
      ],
    };
  },
  created() {
    this.gender = this.$route.query.gender || "male";
    this.renderBook();
    // console.log("created",this.$route.query);
    // console.log("created",this.gender);
  },
  watch: {
    "$route.query.gender"() {
      // console.log("--store-genderUpdate--");
      this.renderBook();
    },
  },
  beforeRouteEnter(to, from, next) {
    // 不！能！获取组件实例 `this`,因为当守卫执行前，组件实例还没被创建
    // 需要通过 `vm` 访问组件实例
    next((vm) => {
      vm.gender = to.query.gender || "male";
      // console.log("beforeRouteEnter", vm.gender);
    });
  },
  beforeRouteUpdate(to, from, next) {
    // 可以访问组件实例 `this`
    this.gender = to.query.gender || "male";
    // console.log("beforeRouteUpdate", this.gender);
    next();
  },
  methods: {
    renderBook() {
      if (this.gender == "male") {
        if (
          sessionStorage.getItem("maleHot") &&
          sessionStorage.getItem("maleFinish")
        ) {
          this.maleHot = JSON.parse(sessionStorage.getItem("maleHot"));
          this.maleFinish = JSON.parse(sessionStorage.getItem("maleFinish"));
          return;
        }

        this.axios.get("/rank/54d42d92321052167dfb75e3").then((response) => {
          this.maleHot = response.data.ranking;
          var mHot = JSON.stringify(response.data.ranking);
          sessionStorage.setItem("maleHot", mHot);
        });

        this.axios.get("/rank/564eb878efe5b8e745508fde").then((response) => {
          this.maleFinish = response.data.ranking;
          var mFinish = JSON.stringify(response.data.ranking);
          sessionStorage.setItem("maleFinish", mFinish);
        });
      } else if (this.gender == "female") {
        if (
          sessionStorage.getItem("femaleHot") &&
          sessionStorage.getItem("femaleFinish")
        ) {
          this.femaleHot = JSON.parse(sessionStorage.getItem("femaleHot"));
          this.femaleFinish = JSON.parse(sessionStorage.getItem("femaleFinish"));
          return;
        }

        this.axios.get("/rank/54d43437d47d13ff21cad58b").then((response) => {
          this.femaleHot = response.data.ranking;
          var fHot = JSON.stringify(response.data.ranking);
          sessionStorage.setItem("femaleHot", fHot);
        });

        this.axios.get("/rank/564eb8a9cf77e9b25056162d").then((response) => {
          this.femaleFinish = response.data.ranking;
          var fFinish = JSON.stringify(response.data.ranking);
          sessionStorage.setItem("femaleFinish", fFinish);
        });
      }
    },
    navigate(n) {
      this.$router.push(`/?gender=${n}`).catch((err) => err);
    },
    toRank() {
      this.$router.push({ name: "Rank" });
    },
    toList(name) {
      if (!this.rankList) {
        var a = this.axios.get("/rank-category").then((response) => {
          this.$store.commit("updateRankList", response.data);
        });
      }
      var g;
      if (a) {
        a.then(() => {
          if (this.gender == "male") {
            g = this.rankList.male;
          } else if (this.gender == "female") {
            g = this.rankList.female;
          }
          var rank = g.filter((ele) => {
            return ele.title.includes(name);
          });
          this.$router.push({
            name: "List",
            query: { title: name, id: rank[0]._id, gender: this.gender },
          });
        });
      } else {
        if (this.gender == "male") {
          g = this.rankList.male;
        } else if (this.gender == "female") {
          g = this.rankList.female;
        }
        var rank = g.filter((ele) => {
          return ele.title.includes(name);
        });
        this.$router.push({
          name: "List",
          query: { title: name, id: rank[0]._id, gender: this.gender },
        });
      }
    },
    toDetail(id) {
      this.$router.push({
        name: "BookDetail",
        query: { id: id },
      });
    },
    toSearch() {
      this.$router.push({ name: "Search" });
    },
  },
};
</script>

<style lang="less" scoped>
.store {
  position: relative;
  padding-bottom: 50px;
  &.male {
    background-image: url("../assets/images/background-male.png");
  }
  &.female {
    background-image: url("../assets/images/background-female.png");
  }
  background-repeat: no-repeat;
  background-size: 200%;
  background-position: -50vw -35vw;
  .swipe {
    width: 95vw;
    margin: 0 auto;
    overflow: hidden;
  }
  .header {
    position: relative;
    .van-search {
      background-color: transparent;
      padding: 10px 20px 0 20px;
    }
    .my-swipe {
      font-size: 14px;
      color: #999;
      text-align: center;
      width: 100%;
      height: 34px;
      line-height: 34px;
      position: absolute;
      top: 10px;
      left: 0;
    }
  }
  .list {
    /deep/ .van-tabbar {
      height: auto;
      padding: 10px 0;
      .van-tabbar-item__text {
        font-size: 14px;
        margin-top: 5px;
      }
      .van-tabbar-item__icon {
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 10px;
        img {
          display: block;
          height: 100%;
        }
      }
    }
  }
}
</style>