<template>
  <div id="app">
    <router-view
      @update:active="active = $event"
      @update:showFooter="showFooter = $event"
      :shortComment.sync="shortComment"
      :longComment.sync="longComment"
    />

    <div class="footer" v-if="showFooter">
      <van-tabbar
        v-model="active"
        active-color="#ff6c5c"
        inactive-color="#000"
      >
        <van-tabbar-item :to="{ name: 'Store' }">
          <span>书城</span>
          <template #icon="props">
            <img :src="props.active ? store.active : store.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item :to="{ name: 'Category',query:{name:'玄幻'}}">
          <span>分类</span>
          <template #icon="props">
            <img :src="props.active ? cate.active : cate.inactive" />
          </template>
        </van-tabbar-item>
        <van-tabbar-item :to="{ name: 'Bookshelf' }">
          <span>书架</span>
          <template #icon="props">
            <img :src="props.active ? bookshelf.active : bookshelf.inactive" />
          </template>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      active: 0, //底部
      store: {
        active: require("@/assets/images/storeActive.png"),
        inactive: require("@/assets/images/store.png"),
      },
      cate: {
        active: require("@/assets/images/bookshelfActive.png"),
        inactive: require("@/assets/images/bookshelf.png"),
      },
      bookshelf: {
        active: require("@/assets/images/categoryActive.png"),
        inactive: require("@/assets/images/category.png"),
      },
      showFooter: true, //是否显示底部
      shortComment: [], //短评
      longComment: [], //长评
    };
  },
  computed: {
    ...mapState(["collect"]),
  },
  watch: {
    collect: function (n) {
      window.localStorage.setItem("collect", JSON.stringify(n));
    },
  },
};
</script>

<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  background-color: #fff;
  font-size: 16px;

  .footer {
    .van-tabbar {
      .van-tabbar-item__icon {
        width: 20px;
        height: 20px;
        img {
          display: block;
          width: 100%;
        }
      }
    }
  }
}
</style>
